












export default {
	name: "graceSwiper",
	props: {
		swiperId :{
			type : String,
			default: ""
		},
		items : {
			type : Array,
			default : function(){return [];}
		},
		indicatorDots:{
			type:Boolean,
			default:true
		},
		interval : {
			default : 5000
		},
		swiperHeight : {
			type : Number,
			default : 100
		}
	},
	data() {
		return {
			runCount : 0,
		}
	}
}
